import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Call from '@/components/Call'

Vue.use(Router)

export default new Router({
  mode: 'history',  
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/call',
      name: 'Call',
      component: Call
    }
  ]
})



