  import bottom from'./components/Footer'
  export default {
    name: 'app',
    data() {
      return{
        transitionName : ''
      }
    },
    components: {
      top, bottom
    },
  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100vh;
}
a{
  text-decoration: none;
}
</style>



